/* THIS GRADIENT BACKGROUND FOUND @ https://freefrontend.com/css-animated-backgrounds/ */
body {
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh;
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
/* END GRADIENT BACKGROUND CODE */
body {
	display: grid;
	grid-template-rows: 1fr 1fr 1fr;
	font-family: 'MedievalSharp', cursive;
	color: white;
	height: 800px;
}
header {
	font-size: 30px;
	text-align: center;
	font-weight: bolder;
	padding: 20px;
	grid-row: 1;
}
nav {
	text-align: center;
	grid-row: 3;
}
a {
	text-decoration: none;
	color: white;
}
.WizardingSchoolsLogo {
	border-radius: 75%;
	display: block;
	margin-left: auto;
	margin-right: auto;
	padding: 0px 0px 15px 0px;
}
